
.alet_container{
    position:fixed;
    z-index:25;
}
.tip_text{
    background: #666;
    color: white;
    padding: 0.25rem 0.5rem;
    position: fixed;
    left:50%;
    -webkit-transform:translateX(-50%);
            transform:translateX(-50%);
    bottom: 3rem;
    font-size: 0.35rem;
    background:rgba(0,0,0,0.75);
    white-space:nowrap;
}



